@use 'base/variables' as v ;
@use 'base/mixins'  as m;

.destinos {
    &__grid {
        @include m.grid(1, 2rem);
    
        @include m.tablet {
            @include m.grid(2, 3rem);
        }
    
        @include m.desktop {
            @include m.grid(4, 3rem);
        }
    }
}

.destino {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 4rem;

    @include m.tablet {
        gap: 2rem;
    }

    &__imagen {
        width: 14rem;
        height: 14rem;
    }

    &__nombre {
        max-width: 12rem;
        margin: 0;
    }

    &__distancia {
        margin: 0;
    }
}